import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import IconButton from '@material-ui/core/IconButton';

import DraftsIcon from '@material-ui/icons/Drafts';
import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';

import PeopleIcon from '@material-ui/icons/People';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';
import AccountBoxIcon from '@material-ui/icons/AccountBox';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import CloseIcon from '@material-ui/icons/Close';
import { connect } from 'react-redux';

const drawerWidth = 240;
const useStyles = makeStyles(theme => ({
	root: {
		display: 'flex'
	},
	nested: {
		paddingLeft: theme.spacing(4)
	},
	drawer: {
		[theme.breakpoints.up('sm')]: {
			width: drawerWidth,
			flexShrink: 0
		}
	},
	drawerPaper: {
		width: drawerWidth
	},
	toolbar: theme.mixins.toolbar,
	closeMenuButton: {
		marginRight: 'auto',
		marginLeft: 0
	}
}));

function LeftPanel(props) {
	const { userLogin } = props;
	const classes = useStyles();
	const theme = useTheme();
	const [open, setOpen] = React.useState(false);

	const handleClick = () => {
		setOpen(!open);
	};

	return (
		<nav className={classes.drawer}>
			<Hidden smUp implementation='css'>
				<Drawer
					variant='temporary'
					anchor={theme.direction === 'rtl' ? 'right' : 'left'}
					open={props.mobileOpen}
					onClose={props.changeDrawerToggle}
					classes={{
						paper: classes.drawerPaper
					}}
					ModalProps={{
						keepMounted: true // Better open performance on mobile.
					}}
				>
					<IconButton onClick={props.changeDrawerToggle} className={classes.closeMenuButton}>
						<CloseIcon />
					</IconButton>
					<MyDrawer
						role={userLogin && userLogin.role}
						changeSelectedOption={value => {
							props.changeSelectedOption(value);
						}}
					/>
				</Drawer>
			</Hidden>
			<Hidden xsDown implementation='css'>
				<Drawer
					className={classes.drawer}
					variant='permanent'
					classes={{
						paper: classes.drawerPaper
					}}
				>
					<div className={classes.toolbar} />
					<MyDrawer
						role={userLogin && userLogin.role}
						changeSelectedOption={value => {
							props.changeSelectedOption(value);
						}}
					/>
				</Drawer>
			</Hidden>
		</nav>
	);
}

const mapStateToProps = ({ authReducers }) => {
	return {
		userLogin: authReducers.userLogin
	};
};

export default connect(mapStateToProps, null)(LeftPanel);

var panelOption = [
	{ title: 'Users', value: 'users', icon: <PeopleIcon />, auth: 'admin' },
	{ title: 'Products', value: 'products', icon: <LibraryBooksIcon /> },
	{ title: 'Brands', value: 'brands', icon: <LibraryBooksIcon />, auth: 'admin' }
];
function MyDrawer(props) {
	if (props.role != 'admin') {
		panelOption = panelOption.filter(item => item.auth != 'admin');
	}
	return (
		<div>
			<List>
				{panelOption.map((option, index) => (
					<ListItem
						key={index}
						button
						onClick={() => {
							props.changeSelectedOption(option.value);
						}}
					>
						<ListItemIcon>{option.icon}</ListItemIcon>
						<ListItemText primary={option.title} />
					</ListItem>
				))}
			</List>
		</div>
	);
}
