import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import AccountCircleOutlinedIcon from '@material-ui/icons/AccountCircleOutlined';
import Fab from '@material-ui/core/Fab';
import { Link, withRouter } from 'react-router-dom';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ExpandMoreOutlinedIcon from '@material-ui/icons/ExpandMoreOutlined';
import AppContext from '../AppContext';
import { connect } from 'react-redux';
import { userActions } from '../auth/store/actions/user.actions';
import { alertActions } from '../components/notifications/alert.actions';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

import AddIcon from '@material-ui/icons/Add';
import { productActions } from '../store/actions/product.action';
import { Grid, Badge } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
	typography: {
		padding: theme.spacing(2)
	},
	appBar: {
		zIndex: theme.zIndex.drawer + 1
	},
	menuButton: {
		marginRight: theme.spacing(2),
		[theme.breakpoints.up('sm')]: {
			display: 'none'
		}
	},
	root: {
		flexGrow: 1
	},
	buttom: {
		margin: theme.spacing(1)
	},
	title: {
		flexGrow: 1,
		textAlign: 'center',
		'& > a': {
			textDecoration: 'none',
			color: 'inherit'
		}
	},
	fab: {
		margin: theme.spacing(1),
		height: '40px'
	},
	loginIcon: {
		marginRight: theme.spacing(1)
	}
}));

function MainAppBar(props) {
	const classes = useStyles();
	const currentUser = props.userLogin ? props.userLogin.user : null;
	const [carList, setCarList] = React.useState([]);

	React.useEffect(() => {
		setCarList(props.listCarProduct);
	}, [props.listCarProduct]);

	const handleSelectItem = name => {
		if (name == 'Logout') {
			props.handleLogoutUser();
		}
	};
	const selectBrandFilter = value => {
		console.log(value);
	};

	return (
		<AppContext.Consumer>
			{context => (
				<React.Fragment>
					<AppBar position='fixed' className={classes.appBar}>
						<Toolbar>
							<IconButton
								color='inherit'
								aria-label='Open drawer'
								edge='start'
								onClick={props.changeDrawerToggle}
								className={classes.menuButton}
							>
								<MenuIcon />
							</IconButton>
							<SimpleMenu
								brandData={props.loadData}
								handleBrandFilter={val => {
									selectBrandFilter(val);
								}}
							/>

							<Typography variant='h6' className={classes.title}>
								<Link to='/shop'>PhoneShop</Link>
							</Typography>
							<Grid
								container
								direction='row'
								justify='center'
								alignItems='center'
								style={{ width: 'unset' }}
							>
								{currentUser ? (
									<UserMenu
										currentUser={currentUser}
										handleSelectItem={name => handleSelectItem(name)}
									/>
								) : (
									<Link style={{ textDecoration: 'none' }} to='/login'>
										<Fab variant='extended' aria-label='delete' className={classes.fab}>
											<AccountCircleOutlinedIcon className={classes.loginIcon} />
											Login
										</Fab>
									</Link>
								)}
								<Link style={{ textDecoration: 'none', color: 'inherit' }} to='/carDetail'>
									<Badge badgeContent={carList && carList.length} color='secondary'>
										<ShoppingCartIcon />
									</Badge>
								</Link>
							</Grid>
						</Toolbar>
					</AppBar>
				</React.Fragment>
			)}
		</AppContext.Consumer>
	);
}

const mapStateToProps = ({ authReducers, brandReducer, productReducer }) => {
	return {
		userLogin: authReducers.userLogin,
		loadData: brandReducer.listBrand,
		listCarProduct: productReducer.listCarProduct
	};
};
const mapDispatchToProps = dispatch => {
	return {
		handleLogoutUser: () => {
			dispatch(userActions.Logout());
		}
	};
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MainAppBar));

function SimpleMenu(props) {
	const { brandData } = props;
	const classes = useStyles();
	const [anchorEl, setAnchorEl] = React.useState(null);

	const handleClick = event => {
		setAnchorEl(event.currentTarget);
	};

	const handleClose = value => {
		setAnchorEl(null);
		props.handleBrandFilter(value);
	};

	return (
		<div>
			<Button
				aria-controls='simple-menu'
				style={{ color: '#fff' }}
				className={classes.button}
				onClick={handleClick}
				endIcon={<ExpandMoreOutlinedIcon />}
			>
				Brands
			</Button>
			<Menu
				id='simple-menu'
				anchorEl={anchorEl}
				keepMounted
				open={Boolean(anchorEl)}
				onClose={handleClose}
			>
				{brandData &&
					brandData.brandResult.map(item => {
						let path = `/brands/${item.name}`;
						return (
							<Link to={path} style={{ textDecoration: 'none', color: 'inherit' }}>
								<MenuItem
									key={item._id}
									onClick={() => {
										handleClose(item._id);
									}}
								>
									{item.name}
								</MenuItem>
							</Link>
						);
					})}
			</Menu>
		</div>
	);
}

function UserMenu(props) {
	const classes = useStyles();
	const [anchorEl, setAnchorEl] = React.useState(null);

	const handleClick = event => {
		setAnchorEl(event.currentTarget);
	};

	const handleClose = event => {
		props.handleSelectItem(event);
		setAnchorEl(null);
	};

	return (
		<div>
			<Fab variant='extended' aria-label='account' onClick={handleClick} className={classes.fab}>
				<AccountCircleOutlinedIcon className={classes.loginIcon} />
				{props.currentUser}
			</Fab>
			<Menu
				id='simple-menu'
				anchorEl={anchorEl}
				keepMounted
				open={Boolean(anchorEl)}
				onClose={handleClose}
			>
				<Link to='/home' style={{ textDecoration: 'none', color: 'inherit' }}>
					<MenuItem onClick={() => handleClose('Profile')}>Profile</MenuItem>
				</Link>
				<Link to='/add' style={{ textDecoration: 'none', color: 'inherit' }}>
					<MenuItem onClick={() => handleClose('NewProduct')}>New Product</MenuItem>
				</Link>
				<MenuItem onClick={() => handleClose('Logout')}>Logout</MenuItem>
			</Menu>
		</div>
	);
}
